@font-face {
  font-family: 'Reddit Sans';

  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'Reddit Sans', sans-serif;
}

.app {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  height: 100dvh;
  background-image: url('assets/img/background.png');
  background-size: cover;
  background-position: center;

  display: flex;
  justify-content: center;
  align-items: center;
  .wrapper {

    img {
      width: 100%;
      height: 100dvh;
      object-fit: cover;
    }
    .envelope {
      display: block;
      cursor: pointer;
      transition: opacity 0.5s ease-in-out;

      &.fade-out {
        opacity: 0;
      }
      &.big {
        display: block;
        @media (max-width: 920px) {
          display: none;
        }
      }
      &.small {
        display: none;
        @media (max-width: 920px) {
          display: block;
        }
      }
    }

  }

}

