.letter {
  box-shadow: 5px 6px 31px 7px rgba(0,0,0,0.1);
  border-radius: 16px;
  line-height: 24px;
  max-width: 800px;
  padding: 60px;
  background: white;
  opacity: 0;
  transition: opacity 0s ease-in-out;
  &.fadeIn {
    opacity: 1;
  }
  @media (max-width: 920px) {
    border-radius: 0;
    padding: 32px;
  }
}